<template>
  <div id="app">
    <!--// 頭部-->
    <div class="title">{{comDatas.title}}</div>
    <!--轮播图-->
    <div class="show-img">
      <template>
        <el-carousel :interval="5000" arrow="false" class="carousel-box">
          <el-carousel-item v-for="images in imgList" :key="images">
            <img  v-bind:src="images" alt="">
          </el-carousel-item>
        </el-carousel>
      </template>
    </div>
    <!--// 商品價錢-->
    <el-row class="sales_wrap" style="width: 95%;margin: 0 auto;">
      <el-col :span="16">
        <div class="sales-price">
          <span class="price">{{comDatas.currency_front_symbol}}{{comDatas.price}}</span>
          <span class="sales_info">
            <div>原價<del>{{comDatas.currency_front_symbol}}{{comDatas.Original_Price}}</del></div>
          </span>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="sales-time">
          <div class="over-test"><img src="@/assets/time.png" alt="">距离结束:</div>
          <div class="timer">
          </div>
        </div>
      </el-col>
      <el-col :span="24">
        <el-progress :percentage='alreadyBought' color="red"></el-progress>
      </el-col>
      <el-col :span="24">
        <div class="title">
          {{comDatas.title}}
          <div class="title-info">{{comDatas.readme}}</div>
        </div>

      </el-col>
      <el-col :span="24">
        <el-collapse>
          <el-collapse-item name="1">
            <template slot="title">
              <span class="p_relative span-icon"><img src="@/assets/wuliu.png" class="show-icon">免邮配送</span>
              <span class="p_relative span-icon"><img src="@/assets/7tian.png" class="show-icon">七天无理由退货</span>
              <span class="p_relative span-icon"><img src="@/assets/hdfk.png" class="show-icon">货到付款</span>
            </template>
            <h2>溫馨提示</h2>
            <div>&nbsp;&nbsp;&nbsp;&nbsp;本產品的實際使用效果根據個人情況決定，不保證每位用戶都能享受到所宣傳的效果。若有疑問請諮詢在綫客服或通過電子郵箱聯絡我們，本公司享有最終解釋權。<br/>
              &nbsp;&nbsp;&nbsp;&nbsp;支持貨到付款+免郵費+7天鑒賞期！收到貨有任何疑問請聯繫我們在線客服! 也可隨時聯繫:{{comDatas.email}}。同時請附上您的姓名及聯繫方式及訂單編號在郵件當中，或者通過LINE：{{comDatas.LINE}}，以便我們能快速的給您及時處理，祝您購物愉快！</div>
          </el-collapse-item>
        </el-collapse>
      </el-col>
      <el-col :span="24">
        <div id="commodity-box"></div>
      </el-col>
    </el-row>

    <!--// 商品內容-->
    <div class="f_footer">
      <div class="footer-box">
        <router-link to="/setMeal">
          <span class="left_span">限量抢购</span>
          <span class="right_span">立即抢购</span>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'APP',
  data: function () {
    return {
      comDatas: [],
      imgList: [],
      alreadyBought : 0,
    }
  },
  created () {
    this.getComData();
  },
  methods: {
    getComData () {
      var http = 'http://cod.hzhanghuan.com';
      var idArr = window.location.search.split("=");
      var that = this;
      var intDiff;
      that.$axios.get( http+"/api/v1/Cod/goodsdetail?id="+idArr[1])
      // this.$axios.get(http + "/api/v1/Cod/goodsdetail?id=54")
        .then(response => {

          var commodityData = response.data.data;
          // console.log(commodityData);
          that.comDatas = commodityData;

          // 图集
          var images = commodityData.images.replace(/&quot;/g, '"');
          var imagesObj = JSON.parse(images);
          for (var i in imagesObj) {
            that.imgList.push(http + imagesObj[i]);
          }
          //商品内容
          var comContent = commodityData.content;
          var detailHtml = comContent;
          $("#commodity-box").append(detailHtml);

          // 已經購買
          var inventory = commodityData.Inventory;  // 庫存
          var sales = commodityData.sales ; //銷量
          that.alreadyBought = Math.round((commodityData.sales / commodityData.Inventory)*10);

          // 倒计时
          intDiff = commodityData.rush_time * 3600 - 1;
          return intDiff;
        });
      window.setInterval(function () {
        var day = 0,
          hour = 0,
          minute = 0,
          second = 0; //时间默认值
        if (intDiff > 0) {
          hour = Math.floor(intDiff / (60 * 60)) - (day * 24);
          minute = Math.floor(intDiff / 60) - (day * 24 * 60) - (hour * 60);
          second = Math.floor(intDiff) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
        }

        var hourDiv = "<span id='h'>" + hour + ":" + "</span>";
        $(".timer").html(hourDiv + minute + ":" + second);

        intDiff--;
      }, 1000);
    },
  }
}

</script>

<style>
.title {
  font-size: 20px;
  padding: 2% 4%;
  border: 1px solid #e6e6e6;
  font-weight: bold;
  line-height: 28px;
}
.card-title {
  display: inline-block;
  border-left: 4px solid #f12222;
  padding-left: 15px;
}
.el-card__header {
  padding: 15px;
}
.box-card {
  width: 98%;
  margin: 20px auto;
}
.card-img {
  width: 100%;
  margin: 0 auto;
}
.card-img img {
  width: 100%;
  height: 100%;
}
.show-img {
  width: 100%;
  height: auto;
}
.show-img img {
  max-width:100%;
  height: 100%;
  display: block;
  margin: 0 auto;
}
.sales-price {
  height: 55px;
}
.price {
  display: block;
  font-size: 24px;
  padding-top: 15px;
  text-indent: 8%;
  font-weight: 700;
  line-height: 20px;
}
.sales_info {
  display: block;
  font-size: 10px;
  color: #666666;
  margin: 5px 0 0 0;
  text-indent: 8%;
}
.sales-time {
  height: 20px;
  color: #666;
  font-size: 8px;
  margin-top: 40px;
}
.over-test {
  float: left;
  position: relative;
  font-weight: bold;
}

.over-test img {
  position: absolute;
  left: -20px;
  top: -1px;
}
.timer {
  float: left;
  font-weight: bold;
  margin-left: 5px;
}
.title {
  line-height: 20px;
  margin: 10px auto;
}
.title-info {
  font-size: 12px;
  color: #666666;
  font-weight: bold;
  margin: 10px 0;
}
.p_relative {
  position: relative;
}
.span-icon {
  padding-left: 20px;
  margin-right: 10px;
}
.show-icon {
  position: absolute;
  top: 0;
  left: 0;
}
/*底部*/
.f_footer {
  position: fixed;
  height: 45px;
  line-height: 45px;
  bottom: 0;
  left: 0;
  width: 100%;
  color: #f1f1f1;
}

.footer-box {
  overflow: hidden;
  max-width: 640px;
  background-color: #0a0a0a;
  margin: 0 auto;
}

.left_span {
  float: left;
  margin-left: 10px;
  color: #ba7;
}

.right_span {
  float: right;
  margin-right: 10px;
  cursor: pointer;
  color: #ba7;
}
#commodity-box {
  margin: 20px auto;
  padding-bottom: 30px;
}
</style>



